.logo{
    position: relative;
}

.logo::before{
    content:'';
    position: absolute;
}

.mainnav ul{
    display: flex;
    justify-content: center;
  }

.mainnav ul li{
    margin: 0 24px;
    margin-top:-150px;
      list-style: none;
      font-weight: bold;
      font-family: monospace;
      font-size: x-large;
      color: green;
} 

.curve {
    position: absolute;
    height: 225px;
    width: 100%;
    bottom: 0;
  }

.curve::before{
    content:'';
    display: block;
    position: absolute;
    border-radius: 100% 50%;
    width: 55%;
    height: 100%;
    background-color: blue;
    transform: translate(85%,60%);
}

.curve::after{
    content:'';
    display: block;
    position: absolute;
    border-radius: 100% 50%;
    width: 55%;
    height: 100%;
    background-color: red;    
    transform: translate(-4%,40%);
    z-index:1;
}

.custom-shape-divider-top-1683378542 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.spacer{
    aspect-ratio: 960/300;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.layer1{
    background-image: url('../../public/NavlayerOne.svg');
}

.flip{
    transform: rotate(180deg);
}

.navlayer{
    background-image: url('../../public/NavlayerOne.svg');
}

.news:hover::after{
    /* transform: scale(1.1); */
    transition: 400ms;
    width: 100%;
  }
  
.news::after{
    content:'';
    display:block;
    height: 4px;
    width:0;
    background:green;
    transition:all .5
  }

.news:hover{
    transform: scale(1.2);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  }


html{
    scroll-behavior: smooth;
}

